.hero {
  overflow: hidden !important;
  margin-top: 48px;

  @include small-tablet {
    margin-bottom: 32px;
  }

  .swiper {
    overflow: hidden !important;
    @include tablet {
      padding-left: 24px;
    }
  }
  &__slider {
    position: relative;
    height: 564px;
    justify-content: center;
    @include small-tablet {
      height: auto;
    }
  }

  &__slide-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 480px !important;
    border-radius: 24px;
    background-color: var(--grey-100);

    @include tablet {
      flex-direction: column-reverse;
      justify-content: start;
    }
    @include small-tablet {
      height: 100% !important;
      border-radius: 12px;
    }
  }
  &__slide-box {
    max-width: 584px;
    display: flex;
    flex-direction: column;
    height: 480px;

    @media (max-width: 1170px) {
      max-width: 535px;
    }
    @include tablet {
      height: 100%;
    }
    @include small-tablet {
      max-width: 400px;
    }
    @include mobile {
      max-width: 100%;
    }
  }
  &__slide-content {
    display: flex;
    flex-direction: column;
    padding: 72px 24px 72px 72px;
    height: 480px;
    @include tablet {
      padding: 34px;
      height: 100%;
    }
    @include small-tablet {
      padding: 24px;
    }
  }

  &__slide-text {
    height: 100%;
    margin-bottom: 15px;
    @include tablet {
      height: auto;
      margin-bottom: 20px;
    }
    @include mobile {
      margin-bottom: 24px;
    }
  }

  &__slide-title {
    margin-bottom: 12px;
    font-weight: 600;
    font-size: 48px;
    line-height: 121.9%;
    color: var(--base-grey);
    @include tablet {
      font-size: 38px;
    }
    @include small-tablet {
      font-size: 30px;
    }
    @include mobile {
      font-size: 24px;
    }
  }
  &__slide-descr {
    @media (max-width: 1170px) {
      font-size: 20px;
    }
    @include small-tablet {
      font-size: 18px;
      display: block;
      display: -webkit-box;
      max-width: 100%;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    @include mobile {
      font-size: 16px;
    }
  }
  &__slide-btn {
    margin-top: auto;
    @include small-tablet {
      width: 100%;
      text-align: center;

      a {
        width: 100%;
        padding: 18px 0;
        justify-content: center;
      }
    }
  }
  &__slider-img {
    width: 480px;
    height: 480px;
    border-top-right-radius: 24px;
    border-bottom-right-radius: 24px;
    img {
      border-top-right-radius: 24px;
      border-bottom-right-radius: 24px;
    }

    @include tablet {
      width: 100%;
      height: 170px;
      border-bottom-right-radius: 0;
      border-top-left-radius: 24px;

      img {
        width: 100%;
        height: 100%;
        border-bottom-right-radius: 0;
        border-top-left-radius: 24px;
      }
    }

    @include small-tablet {
      width: 100%;
      height: 128px;
      border-top-left-radius: 12px;
      border-top-right-radius: 12px;

      img {
        width: 100%;
        height: 100%;
        border-top-left-radius: 12px;
        border-top-right-radius: 12px;
      }
    }
  }

  .swiper-slide {
    width: auto !important;
    @include tablet {
      height: auto;
    }
    @include small-tablet {
      width: 400px !important;
    }
    @include mobile {
      width: 318px !important;

    }
  }

  .swiper__btns {
    position: absolute;
    width: 1180px;
    height: 85%;
    left: calc(50% - 590px);
    top: 0;
    z-index: 2;
    @media (max-width: 1170px) {
      width: 1014px;
      height: 85%;
      left: calc(50% - 507px);
    }
    @include tablet {
      display: none;
    }
  }
  .swiper-button-prev::after {
    font-family: 'icons';
    font-size: 24px;
    color: var(--base-black) !important;
    content: "\e97b";
    transition: color 0.3s ease-in;
  }
  .swiper-button-next::after {
    font-family: 'icons';
    font-size: 24px;
    color: var(--base-black) !important;
    content: "\e97c";
    transition: color 0.3s ease-in;
  }
  .swiper-button-prev:hover::after,
  .swiper-button-next:hover::after {
    color: var(--cyan-400) !important;
  }
  .swiper-pagination {
    bottom: 36px !important;
  }
  .swiper-pagination-bullet {
    width: 12px !important;
    height: 12px !important;
    background: var(--grey-300) !important;
    opacity: 1 !important;
  }
  .swiper-pagination-bullet-active {
    background: var(--cyan-400) !important;
  }
}
