.interface {
  padding-top: 24px;
  background-color: var(--grey-100);

  &__title {
    margin-bottom: 12px;

    @include mobile {
      font-size: 28px;
    }

    @media (max-width: 400px) {
      font-size: 22px;
    }
  }

  &__descr {
    margin-bottom: 48px;

    @include small-tablet {
      margin-bottom: 32px;
    }
  }

  &__img {
    display: block;
    max-width: 1076px;
    margin-left: auto;
    margin-right: auto;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;

    @media (max-width: 390px) {
      display: none;
    }

    img {
      border-top-left-radius: 25px;
      border-top-right-radius: 25px;
      box-shadow: 0px -8px 22px -6px rgba(70, 99, 92, 0.12);

      @media (max-width: 390px) {
        display: none;
      }
    }
  }

  &__mob {
    display: block;
    max-width: 80%;
    border-top-left-radius: 15px;
    margin-right: -24px;
    margin-left: auto;

    @media (min-width: 390px) {
      display: none;
    }

    img {
      margin-right: -24px;
      border-top-left-radius: 15px;
      box-shadow: 0px -8px 22px -6px rgba(70, 99, 92, 0.12);

      @media (min-width: 390px) {
        display: none;
      }
    }
  }
}
